body {
  background-color: #5bbad5;
}
/* login page */
.qr {
  border-radius: 30px;
  padding: 10px;
}
.container {
  height: auto;
}
.card {
  border-radius: 20px;
  padding: 20px;
}
.qr-title {
  font-family: Outfit, 700;
  font-size: 22px;
  font-weight: bold;
}
.qr-text {
  font-family: Outfit, 400;
  font-size: 15px;
  color: hsl(220, 15%, 55%);
}
/*Form1*/
.info {
  font-family: Montserrat 500;
  text-align: center;
  text-decoration: underline;
}
.inpbox {
  width: 300px;
}

.table-custom {
  --bs-table-bg: transparent !important;
}
.span-container {
  display: flex;
  justify-content: flex-start;
}
.claimInput {
  border-radius: 20px;
}

/* options */
.bgim {
  background: no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.optbtn {
  width: 300px;
  margin: 15px;
}
@media screen and (max-width: 420px) {
  .optbtn {
    width: 150px;
    margin: 10px 0px;
  }
  .optlogo {
    width: 150px;
  }
}

/* forms */

@media screen and (max-width: 420px) {
  .formwid {
    width: fit-content;
  }
}
